body {
  background-color: #000;
  margin: 0;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.header {
  z-index: 1;
  background-color: #000;
  border-radius: 0 0 80% 80%;
  justify-content: center;
  width: 100%;
  margin: auto;
  display: flex;
  position: fixed;
  top: 0;
  overflow: hidden;
  box-shadow: 0 0 15px 15px #000;
}

#boton {
  color: #fff;
  background-color: #3a5d66;
  border: 3px inset #21d7b4;
  border-radius: 10px 10px 70% 70%;
  margin: 220px auto auto;
  padding: 12px 22px;
  font-size: 20px;
  display: block;
}

#boton:hover {
  cursor: pointer;
  color: #000;
  background-color: #21d7b4;
  border: 3px solid #21d7b4;
  box-shadow: 0 0 10px 1px #21d7b4;
}

.header > img {
  width: 30rem;
  height: 12rem;
}

#campeones {
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 10px;
  margin-top: 200px;
  display: grid;
}

img {
  border-radius: 10px 10px 0 0;
  transition: filter .2s, transform .2s;
}

.card {
  text-align: center;
  background-color: #3c3c41;
  border: 1px solid #3c3c41;
  border-radius: 10px;
  margin: auto;
  transition: transform .2s;
}

.imagen {
  border-radius: 10px 10px 0 0;
  overflow: hidden;
}

.card img:not(hover) {
  filter: grayscale(.5) blur(2px);
}

.card img:hover {
  filter: grayscale(0) blur() saturate(170%);
  transform: scale(1.1);
}

.card:hover .tipo, .card:hover .nombre {
  display: block;
}

.card:hover {
  cursor: pointer;
  transform: translateY(-8px);
}

.nombre {
  background: linear-gradient(0deg, #785a28, #c89b3c);
  font-weight: bold;
  display: none;
}

.tipo {
  background: linear-gradient(0deg, #005a82, #0ac8b9);
  border-radius: 0 0 10px 10px;
  display: none;
}
